
import { defineComponent, onMounted, ref, watch } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";

import EDMTemplateCard from "@/components/EDMTemplateCard.vue";
import { PostCreateEdm, PostDuplicateEdm } from "@/typings/store";
import { EdmCardResponse, EdmParams } from "@/typings/edm";
import { ElNotification } from "element-plus";
import { loading } from "@/utils";
import { getEdms, deleteEdm } from "@/apis/edm";
import { getCompanyTeams } from "@/apis/company";
import { OptionItem } from "@/typings/asideFilter";
import { MemberTeam } from "@/typings/member";
import { formatImageUrl } from "@/utils/image";

const Component = defineComponent({
  name: "EDM",
  components: {
    EDMTemplateCard
  },
  setup: function() {
    const router = useRouter();
    const store = useStore();

    const list = ref<EdmCardResponse[]>([]);
    const teamValue = ref(-1);
    const teamOptions = ref<OptionItem[]>([]);

    watch([teamValue], async (value: number[]) => {
      const id = value[0] > 0 ? value[0] : undefined;
      const { data } = await getEdms(id);
      list.value = data;
    });

    const init = async () => {
      const loadingInstance = loading();
      try {
        const { data: teams } = await getCompanyTeams();
        const { data: edms } = await getEdms();

        teamOptions.value = teams.map(
          (it: MemberTeam): OptionItem => ({
            label: it.name,
            value: it.id,
            checked: false
          })
        );
        list.value = edms;
        console.log(edms);
      } finally {
        loadingInstance.close();
      }
    };

    onMounted(init);

    const onCreateClick = async () => {
      const params: EdmParams = {
        subject: `New EDM`,
        message: "Description",
        message_body: "Message to Receiver",
        html_message: "<div></div>",
        chosen_collection: null
      };
      const { id } = await store.dispatch(PostCreateEdm, params);
      ElNotification({
        title: "Success",
        type: "success",
        message: `EDM has been created`
      });
      await router.push({ name: "EDMDetail", params: { id } });
    };

    const onCardClick = async (id: number) => {
      await router.push({ name: "EDMDisplay", params: { id } });
    };

    const onDuplicateClick = async (item: EdmCardResponse) => {
      await store.dispatch(PostDuplicateEdm, item.id);
      ElNotification({
        title: "Success",
        type: "success",
        message: `EDM ${item.subject} duplicated successfully.`
      });
      await init();
    };

    const onDeleteClick = async (item: EdmCardResponse) => {
      const { data } = await deleteEdm(item.id);
      ElNotification({
        title: "Success",
        type: "success",
        message: data.message
      });
      await init();
    };

    return {
      list,
      teamValue,
      teamOptions,

      onCreateClick,
      onCardClick,
      onDuplicateClick,
      onDeleteClick,

      formatImageUrl,
    };
  }
});

export default Component;
